.login-container {
  background: linear-gradient(
      116.64deg,
      rgba(0, 0, 0, 0.8) 0.46%,
      rgba(0, 0, 0, 0) 100%
    ),
    url('https://res.cloudinary.com/reddyimgs/image/upload/v1686991946/netfilx_1_b0vrsu.png');
  height: 100vh;
  width: 100vw;
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
}

.login-logo {
  position: absolute;
  /* width: 180px; */
  height: 42px;
  left: 105px;
  top: 30px;
}

.login-card {
  height: 440px;
  width: 456px;

  border-radius: 8px;
  opacity: 0.8;
  background: #0c0b10;
}

.login-heading {
  height: 40px;
  font-size: 32px;
  font-weight: 600;
  line-height: 40px;
  letter-spacing: 0em;
  text-align: center;
  margin-top: 64px;
}

.form-container {
  margin-left: 48px;
  margin-right: 48px;
}

.username-input-container {
  display: flex;
  flex-direction: column;
}
.password-input-container {
  display: flex;
  flex-direction: column;
}

.username-label {
  width: 65px;
  height: 16px;
  font-style: normal;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 0.01em;
  text-transform: uppercase;
  margin-top: 32px;
  margin-bottom: 8px;
}

.password-label {
  width: 65px;
  height: 16px;
  font-style: normal;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 0.01em;
  text-transform: uppercase;
  margin-top: 24px;
  margin-bottom: 8px;
}

.username-input {
  height: 40px;
  top: 24px;
  color: #fff;
  background: #333333;
  border-radius: 2px;
  border: 0px;
  outline: none;
  padding: 8px;
  padding-left: 16px;
}

.error-message {
  margin-top: 12px;
  width: 205px;
  height: 18px;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #dc2626;
}

.login-button {
  height: 40px;
  width: 360px;
  border-radius: 8px;
  margin-top: 32px;
  color: #f8fafc;
  background: #e50914;
  border-radius: 8px;
  border: 0px;
  font-size: 14px;
  font-weight: 700;
  line-height: 24px;
  letter-spacing: 0em;
  text-align: center;
}
